import { action, computed, observable } from 'mobx'
import GridStore from './GridStore'
import { FindUnion } from './Union'

export class Player {
  constructor (name) {
    this.isHuman = (name !== undefined)
    this.name = name
    this.gamesWon = 0
    this.position = 0 // should be one or two
  }
  addWin () { this.gamesWon++ }
  getName () { return this.name } // Possible problem for computer player
  getPosition () { return this.position }
  setPosition (position) {
    if (Number.isInteger(position) && position <= 2) {
      this.position = position
    }
  }
}

/**
 * Top Leve data store that composes the other stores
 */
class GameStore {
  @observable grid = new GridStore()
  @observable currentPlayer = null

  constructor (firstPlayer, secondPlayer) {
    const checkParams = player => {
      if (player instanceof Player) return player
      return new Player()
    }
    this.firstPlayer = checkParams(firstPlayer)
    this.firstPlayer.setPosition(1)
    this.secondPlayer = checkParams(secondPlayer)
    this.secondPlayer.setPosition(2)
    this.toggleCurrentPlayer()
  }
  @action toggleCurrentPlayer () {
    if (this.currentPlayer === null) this.currentPlayer = this.firstPlayer
    else if (this.currentPlayer === this.firstPlayer) this.currentPlayer = this.secondPlayer
    else if (this.currentPlayer === this.secondPlayer) this.currentPlayer = this.firstPlayer
  }

  @action endTurn (row, column) {
    this.grid.setCoordinate(row, column, this.currentPlayer)
    const find = new FindUnion()
    find.setUp()
    find.percolate(this.grid.getGrid())
    this.toggleCurrentPlayer()
  }

  @computed get currentTurn () {
    return () => this.currentPlayer.getPosition()
  }

  @action resetGame () {
    this.grid.resetGame()
    this.currentPlayer = this.firstPlayer
  }
}

const store = new GameStore()
export default store



