import { action, observable } from 'mobx'

class Coordinate {
  @observable selected = false
  @observable whoSelected = 0
  constructor (row, index) {
    this.row = row
    this.index = index
  }

  markSelected (player) {
    this.selected = true
    this.whoSelected = player.getPosition()
  }

  getClassName () {
    let column = ''
    switch (this.index) {
      case 1:
        column = 'first'
        break
      case 2:
        column = 'second'
        break
      case 3:
        column = 'third'
        break
      default:
        return null
    }
    return this.row + ' ' + column
  }

  @action clear () {
    this.selected = false
    this.whoSelected = 0
  }

  static makeRow (row) {
    return [new Coordinate(row, 1), new Coordinate(row, 2), new Coordinate(row, 3)]
  }
}

class GridStore {

  @observable topRow = Coordinate.makeRow('top')
  @observable middleRow = Coordinate.makeRow('middle')
  @observable bottomRow = Coordinate.makeRow('bottom')

  @action setCoordinate (row, column, player) {
    switch (row) {
      case 'top':
        this.topRow[column].markSelected(player)
        return
      case 'middle':
        this.middleRow[column].markSelected(player)
        return
      case 'bottom':
        this.bottomRow[column].markSelected(player)
        return
      default:
        return
    }
  }

  getGrid () {
    return ([
      this.topRow.map(e => e.whoSelected),
      this.middleRow.map(e => e.whoSelected),
      this.bottomRow.map(e => e.whoSelected)
    ])
  }

  @action resetGame () {
    this.topRow.map(e => e.clear())
    this.middleRow.map(e => e.clear())
    this.bottomRow.map(e => e.clear())
  }
}

export default GridStore



