class Union {
  constructor (x, y, state) {
    this.x = x
    this.y = y
    this.state = state
    this.union = []
    this.visited = false
  }

  addUnion (union) {
    if (union === this) return
    if (union instanceof Union) {
      if (this.state === -1 || union.state === -1) this.union.push(union)
      if (union.state === this.state) this.union.push(union)
    }
  }

  search (end) {
    this.visited = true
    for (let index in this.union) {
      if (end.compare(this.union[index])) {
        return true
      }
      if (this.union[index].visited !== true) this.union[index].search(end)
    }
  }

  clear () {
    for (let index in this.union) {
      if (this.union[index].visted === true) {
        this.union[index].visited = false
        this.union[index].clear()
      }
    }
  }

  compare (obj) {
    if (obj instanceof Union) {
      if (this.state === obj.state &&
         this.x === obj.x && this.y === obj.y) {
        return true
      }
    }
    return false
  }
}

export class FindUnion {
  constructor () {
    this.UPPER_BOUND = 3
    this.LOWER_BOUND = -1
    this.top = new Union(-1, -1, -1)
    this.bottom = new Union(3, 3, -1)
    this.left = new Union(-1, -1, -1)
    this.right = new Union(3, 3, -1)
    this.grid = {}
    this.array = []
  }

  percolate (array) {
    this.array = array
    for (let y = 0; y < array.length; y++) {
      for (let x = 0; x < array[y].length; x++) {
        if (array[y][x] < 1) continue
        let coordinate = this.makeUnion([x, y])
        this.checkSides(coordinate)
      }
    }
    this.bottom.search(this.top)
    this.right.search(this.left)
    this.bottom.clear()
    this.right.clear()
  }

  checkSides (union) {
    const { x, y } = union
    if ((x - 1) > this.LOWER_BOUND) {
      if (this.array[y][x - 1] > 0) {
        union.addUnion(this.makeUnion([x - 1, y]))
      }
    }
    if ((x + 1) < this.UPPER_BOUND) {
      if (this.array[y][x + 1] > 0) {
        union.addUnion(this.makeUnion([x + 1, y]))
      }
    }
    if ((y - 1) > this.LOWER_BOUND) {
      if (this.array[y - 1][x] > 0) {
        union.addUnion(this.makeUnion([x, y - 1]))
      }
    }
    if ((y + 1) > this.UPPER_BOUND) {
      if (this.array[y + 1][x] > 0) {
        union.addUnion(this.makeUnion([x, y + 1]))
      }
    }
    if (x === y) {
      if ((x - 1) > this.LOWER_BOUND && (y - 1) > this.LOWER_BOUND) {
        if (this.array[y - 1][x - 1] > 0) {
          union.addUnion(this.makeUnion([y - 1][x - 1]))
        }
      }
      if ((x + 1) > this.UPPER_BOUND && (y - 1) > this.LOWER_BOUND) {
        if (this.array[y - 1][x + 1] > 0) {
          union.addUnion(this.makeUnion([y - 1][x + 1]))
        }
      }
      if ((x - 1) > this.LOWER_BOUND && (y + 1) > this.UPPER_BOUND) {
        if (this.array[y + 1][x - 1] > 0) {
          union.addUnion(this.makeUnion([y - 1][x + 1]))
        }
      }
      if ((x + 1) > this.UPPER_BOUND && (y + 1) > this.UPPER_BOUND) {
        if (this.array[y + 1][x + 1] > 0) {
          union.addUnion(this.makeUnion([y + 1][x + 1]))
        }
      }
    }
  }

  makeUnion (points) {
    if (points instanceof Array) {
      let coordinate
      if (this.grid[points[1].toString()][points[0].toString()] === null) {
        coordinate = new Union(points[0], points[1], this.array[points[1]][points[0]])
        this.grid[points[1].toString()][points[0].toString()] = coordinate
        if (points[1] === 0) {
          this.top.addUnion(coordinate)
          coordinate.addUnion(this.top)
        } else if (points[1] === 2) {
          this.bottom.addUnion(coordinate)
          coordinate.addUnion(this.bottom)
        } else if (points[0] === 0) {
          this.left.addUnion(coordinate)
          coordinate.addUnion(this.left)
        } else if (points[0] === 2) {
          this.right.addUnion(coordinate)
          coordinate.addUnion(this.right)
        }
      } else coordinate = this.grid[points[1].toString()][points[0].toString()]
      return coordinate
    }
    return null
  }

  setUp () {
    this.array = []
    this.top = new Union(-1, -1, -1)
    this.bottom = new Union(3, 3, -1)
    this.grid = {
      '0': { '0': null, '1': null, '2': null },
      '1': { '0': null, '1': null, '2': null },
      '2': { '0': null, '1': null, '2': null }
    }
  }
}

export default Union



