import React, { Component } from 'react'
import { observer } from 'mobx-react'
import gameStore from '../stores/GameStore'

@observer class Players extends Component {
  render () {
    const turn = this.props.turn()
    const playerOneStyle = turn === 1 ? 'player active' : 'player'
    const playerTwoStyle = turn === 2 ? 'player active' : 'player'
    return (
      <div className='sidebard'>
        <div className={playerOneStyle}>
          <h2>Player One</h2>
        </div>
        <div className={playerTwoStyle}>
          <h2>Player Two</h2>
        </div>
        <div className='reset'>
          <button onClick={gameStore.resetGame.bind(gameStore)} >
              Reset Game
          </button>
        </div>
        <div className='softText'>
            The border around each player's name determines who's turn it is. <br />
            Green indicates the current turn, while the grey indicates neutral.
            Simply click a tile to mark it during your turn
            Player One is always X
            Player Two is always Y
            Reset the Game whenever with the button above
        </div>
      </div>
    )
  }
}

export default Players



