import React, { Component } from 'react'
import { observer } from 'mobx-react'
import GridBox from './GridBox'
import gameStore from '../stores/GameStore'

@observer class Grid extends Component {
  buildGrid (row, id) {
    const sign = this.props.turn() === 1 ? ' X' : ' O'
    return row.map((e, i) => {
      const props = {
        checked: (e.selected) ? e.whoSelected : null,
        click: () => {
          if (!e.selected) gameStore.endTurn(e.row, i)
          return
        },
        sign,
        style: e.getClassName()
      }
      return <GridBox key={i} {...props} />
    })
  }

  render () {
    const { topRow, middleRow, bottomRow } = this.props.grid
    return (
      <div className='Grid'>
        <ul>
          { this.buildGrid(topRow) }
        </ul>
        <ul>
          { this.buildGrid(middleRow) }
        </ul>
        <ul>
          { this.buildGrid(bottomRow) }
        </ul>
      </div>
    )
  }
}

export default Grid



