import React from 'react'
import Grid from './Grid'
import Players from './Players'
import '../sass/App.sass'

const App = props => {
  return (
    <div className='App'>
      <Players
        playerOne={props.store.firstPlayer}
        playerTwo={props.store.firstPlayer}
        turn={props.store.currentTurn} />
      <Grid
        grid={props.store.grid}
        turn={props.store.currentTurn} />
    </div>
  )
}

export default App



