import React, { Component } from 'react'
import { observer } from 'mobx-react'

@observer class GridBox extends Component {
  constructor () {
    super()
    this.state = {
      style: ''
    }
  }

  componentWillMount () {
    this.setState({style: this.props.style})
  }

  mouseOut () {
    this.setState({ style: this.props.style })
  }

  mouseOver () {
    const { style, sign } = this.props
    this.setState({ style: style + sign })
  }
  render () {
    if (this.props.checked !== null) {
      const sign = this.props.checked === 1 ? ' X' : ' O'
      return <li className={this.state.style + sign} />
    }
    return (
      <li className={this.state.style}
        onMouseOver={this.mouseOver.bind(this)}
        onMouseOut={this.mouseOut.bind(this)}
        onClick={this.props.click} />
    )
  }
}

export default GridBox



